import React, { Component } from 'react';
import './Styles/NutritionTable.css';

export default class NutritionTable extends Component {
  render() {
    return (
      <div
        className='nutritionTableInfoDetail'
        style={{
          backgroundColor: window.location.hash.includes('#MealViewer')
            ? 'var(--background-kinda-white)'
            : 'var(--background-white)',
        }}
      >
        <div className='nutritionTableInfoTitles'>
          {this.props.kcal ? <p className='nutritionTableInfoMain'> </p> : ''}
          <p className='nutritionTableInfoMain'></p>
          <p className='nutritionTableInfoMain'></p>
          <p className='nutritionTableInfoSub'></p>
          <p className='nutritionTableInfoSub'></p>
          <p className='nutritionTableInfoMain'></p>
          <p className='nutritionTableInfoSub'> </p>
          <p className='nutritionTableInfoSub'>  </p>
          <p className='nutritionTableInfoMain'></p>
          <p className='nutritionTableInfoSub'></p>
          <p className='nutritionTableInfoSub'></p>
          <p className='nutritionTableInfoSub'></p>
        </div>
        <div className='nutritionTableInfoValues'>
          {this.props.kcal ? <p className='nutritionTableInfoMain'>{Number(this.props.kcal.toFixed(1))}</p> : ''}
          <p className='nutritionTableInfoMain'>{Number(this.props.protein.toFixed(1))} </p>
          <p className='nutritionTableInfoMain'>{Number(this.props.carb.toFixed(1))} </p>
          <p className='nutritionTableInfoSub'>{Number(this.props.fiber.toFixed(1))} </p>
          <p className='nutritionTableInfoSub'>{Number(this.props.sugar.toFixed(1))} </p>
          <p className='nutritionTableInfoMain'>{Number(this.props.fat.toFixed(1))} </p>
          <p className='nutritionTableInfoSub'>{Number(this.props.saturatedFat.toFixed(2))} </p>
          <p className='nutritionTableInfoSub'>{Number(this.props.unsaturatedFat.toFixed(2))} </p>
          <p className='nutritionTableInfoMain' style={{ color: 'rgb(0 ,0 ,0 ,0)' }}>
            ......
          </p>
          <p className='nutritionTableInfoSub'>{this.props.cholesterol.toFixed(0)} </p>
          <p className='nutritionTableInfoSub'>{this.props.sodium.toFixed(0)} </p>
          <p className='nutritionTableInfoSub'>{this.props.potassium.toFixed(0)} </p>
        </div>
      </div>
    );
  }
}
